<template>
    <div class="card">
        <div class="card-header">
            <strong>Usuário</strong> Manutenção
        </div>
        <div class="card-body card-block">
            <form action="" method="post" class="">
                <div class="row form-group">
                    <div class="col col-md-6">
                        <label for="nf-nome" class=" form-control-label">Nome</label>
                        <input type="nome" id="nf-nome" name="nf-nome" placeholder="" class="form-control">
                        <span class="help-block"></span>
                    </div>
                    <div class="col col-md-6">
                        <label for="nf-email" class=" form-control-label">E-mail</label>
                        <input type="email" id="nf-email" name="nf-email" placeholder="" class="form-control">
                        <span class="help-block"></span>
                    </div>
                </div>
                <div class="row form-group">
                    <div class="col col-md-6">
                        <label for="nf-password" class=" form-control-label">Senha</label>
                        <input type="password" id="nf-password" name="nf-password" placeholder="" class="form-control">
                        <span class="help-block"></span>
                    </div>
                    <div class="col col-md-6">
                        <label for="nf-password" class=" form-control-label">Confimar senha</label>
                        <input type="password" id="nf-password" name="nf-password" placeholder="" class="form-control">
                        <span class="help-block"></span>
                    </div>
                </div>
            </form>
        </div>
        <div class="card-footer-1">
            <button type="button" class="btn btn-primary btn-sm">Salvar</button>
            <button type="button" class="btn btn-secondary btn-sm">Cancelar</button>
        </div>
    </div>
</template>

<script>
export default {
  name: 'ManutencaoUsuario',
  methods: {
    click () {
      // do nothing
    }
  }
}
</script>


<style scoped>

  .input-group-addon {
    background-color: transparent;
    border-left: 0;
  }
    .card .card-footer-1 {
        padding: 0.65rem 1.25rem;
        background-color: #f0f3f5;
        border-top: 1px solid #c2cfd6;
    }
</style>


